const uriAttrs = [
  'background',
  'cite',
  'href',
  'itemtype',
  'longdesc',
  'poster',
  'src',
  'xlink:href'
]


const ARIA_ATTRIBUTE_PATTERN = /^aria-[\w-]*$/i

export const DefaultWhitelist = {
  // html
  '*': ['class', 'dir', 'id', 'lang', 'role', ARIA_ATTRIBUTE_PATTERN],
  a: ['target', 'href', 'title', 'rel'],
  area: [],
  b: [],
  br: [],
  col: [],
  code: [],
  div: [],
  em: [],
  hr: [],
  h1: [],
  h2: [],
  h3: [],
  h4: [],
  h5: [],
  h6: [],
  i: [],
  img: ['src', 'alt', 'title', 'width', 'height'],
  li: [],
  ol: [],
  p: [],
  pre: [],
  s: [],
  small: [],
  span: [],
  sub: [],
  sup: [],
  strong: [],
  u: [],
  ul: []
}

/**
 * URL
 *
 * Shoutout to Angular 7 https://github.com/angular/angular/blob/7.2.4/packages/core/src/sanitization/url_sanitizer.ts
 */
const SAFE_URL_PATTERN = /^(?:(?:https?|mailto|ftp|tel|file):|[^&:/?#]*(?:[/?#]|$))/gi

/**
 * dataURL
 *
 * Shoutout to Angular 7 https://github.com/angular/angular/blob/7.2.4/packages/core/src/sanitization/url_sanitizer.ts
 */
const DATA_URL_PATTERN = /^data:(?:image\/(?:bmp|gif|jpeg|jpg|png|tiff|webp)|video\/(?:mpeg|mp4|ogg|webm)|audio\/(?:mp3|oga|ogg|opus));base64,[a-z0-9+/]+=*$/i

// attrhtmlallowedAttributeList
function allowedAttribute(attr, allowedAttributeList) {
  // attr
  const attrName = attr.nodeName.toLowerCase()

  // allowedAttributeListattrName-1
  if (allowedAttributeList.indexOf(attrName) !== -1) {
    // uriAttrsattrName
    if (uriAttrs.indexOf(attrName) !== -1) {
      // 
      // attrURLdataURLtrue
      // false
      return Boolean(attr.nodeValue.match(SAFE_URL_PATTERN) || attr.nodeValue.match(DATA_URL_PATTERN))
    }

    // true
    return true
  }

  // allowedAttributeList
  const regExp = allowedAttributeList.filter((attrRegex) => attrRegex instanceof RegExp)

  // attrNameregExp
  for (let i = 0, l = regExp.length; i < l; i++) {
    if (attrName.match(regExp[i])) {
      return true
    }
  }

  return false
}

export function sanitizeHtml(unsafeHtml, whiteList, sanitizeFn) {
  // unsafeHtml
  if (unsafeHtml.length === 0) {
    return unsafeHtml
  }

  // sanitizeFntruefunctionsanitizeFn
  if (sanitizeFn && typeof sanitizeFn === 'function') {
    return sanitizeFn(unsafeHtml)
  }

  const domParser = new window.DOMParser()
  // DOM
  const createdDocument = domParser.parseFromString(unsafeHtml, 'text/html')
  // whitelistwhitelistKeys
  const whitelistKeys = Object.keys(whiteList)
  // createdDocument1
  const elements = [].slice.call(createdDocument.body.querySelectorAll('*'))

  // 
  for (let i = 0, len = elements.length; i < len; i++) {
    // elelementsi
    const el = elements[i]
    // nodeName
    // nodeNameClass
    const elName = el.nodeName.toLowerCase()

    // el.nodeNamewhitelistKeys
    // '*'
    if (whitelistKeys.indexOf(el.nodeName.toLowerCase()) === -1) {
      // el
      el.parentNode.removeChild(el)

      // for
      continue
    }

    // el
    // roleclass
    const attributeList = [].slice.call(el.attributes)
    // whiteList[*]
    // whiteList[elName]element
    // []
    const whitelistedAttributes = [].concat(whiteList['*'] || [], whiteList[elName] || [])

    // attributeList(htmlclass)
    attributeList.forEach((attr) => {
      // attr
      if (!allowedAttribute(attr, whitelistedAttributes)) {
        // Elements
        el.removeAttribute(attr.nodeName)
      }
    })
  }

   // HTML
  return createdDocument.body.innerHTML
}
